<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.productOffer.product.preview.file.url" width="120" height="120"
      :alt="item.productOffer.title">
    </div>
    <h3 class="product__title">
      {{ item.productOffer.title }}
    </h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{backgroundColor: item.color.color.code}"></i>
        {{item.color.color.title}}
      </span>
    </p>
    <span class="product__code">
      {{ item.productOffer.id }}
    </span>

    <div class="product__counter form__counter">

      <ReduceButton @click="reduceAmount"/>

      <input type="text" v-model.number="amount" name="count">

      <IncreaseButton @click="increaseAmount"/>

    </div>

    <b class="product__price">
      {{ (item.price * item.quantity) | numberFormat }} ₽
    </b>

    <button @click.prevent="deleteProduct(basketItemId)"

    class="product__del button-del" type="button" aria-label="Удалить товар из корзины">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import IncreaseButton from '@/components/IncreaseButton.vue';
import ReduceButton from '@/components/ReduceButton.vue';
import { mapActions } from 'vuex';

export default {
  data() {
    return {
      basketItemId: this.item.id,
    };
  },
  props: ['item'],
  filters: { numberFormat },
  components: { IncreaseButton, ReduceButton },
  methods: {
    ...mapActions({ deleteProduct: 'deleteProductInCart' }),

    increaseAmount() {
      this.amount += 1;
    },
    reduceAmount() {
      if (this.amount >= 2) {
        this.amount -= 1;
      }
    },
  },
  computed: {
    amount: {
      get() {
        return this.item.quantity;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { basketItemId: this.basketItemId, quantity: value });
      },
    },
  },
};
</script>
