<!-- eslint-disable max-len -->
<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        {{ countProducts | pluralize }}
      </span>
    </div>

    <div class="content__catalog">

      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :code.sync="filterCode" :prop.sync="filterProp" />

      <section class="catalog">
        <div v-if="productsLoading" class="loading">
          <img class="cart-gif" src="@/assets/gif/blocks.gif" alt="Двигающиеся блоки">
        </div>
        <div v-else-if="productsLoadingFailed">
          <span>Ошибка :( Попробуйте</span>
          <router-link :to="{ name: 'main' }" custom v-slot="{ navigate }">
            <button @click="navigate" @keypress.enter="navigate" role="link" class="error-btn">
              еще раз
            </button>
          </router-link>
        </div>
        <ProductList v-else :products="products" />
        <BasePagination v-model="page" :count="countProducts" :per-page="productPerPage" />
      </section>

    </div>
  </main>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import pluralize from '@/helpers/pluralize';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  name: 'MainPage',
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      // filterColor: [],
      filterCode: null,
      filterProp: null,

      page: 1,
      productPerPage: 12,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  filters: { pluralize },
  components: {
    ProductList, BasePagination, ProductFilter,
  },
  computed: {
    products() {
      return this.productsData ? this.productsData.items : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
    categoryId() {
      return this.$route.params.id ? this.$route.params.id : this.filterCategoryId;
    },
    // eslint-disable-next-line consistent-return, vue/return-in-computed-property
    queryParams() {
      if (this.filterProp) {
        return (`props[${this.filterCode}][]`);
      }
    },
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      axios
        .get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productPerPage,
            categoryId: this.categoryId,
            minPrice: this.filterPriceFrom ? this.filterPriceFrom : null,
            maxPrice: this.filterPriceTo ? this.filterPriceTo : null,
            // colorId: this.filterColor,
            [this.queryParams]: this.filterProp,
          },
        })
        // eslint-disable-next-line no-return-assign
        .then((response) => this.productsData = response.data)
        // eslint-disable-next-line no-return-assign
        .catch(() => this.productsLoadingFailed = true)
        // eslint-disable-next-line no-return-assign
        .then(() => this.productsLoading = false);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    categoryId() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    // filterColor() {
    //   this.loadProducts();
    // },
  },
  created() {
    this.loadProducts();
  },
};
</script>

<style scoped>
.loading {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #fff;
  justify-content: center;
  align-items: center;
}

.cart-gif {
  width: 100px;
  height: 100px;
}

.error-btn {
  background-color: #fff;
}
</style>
