<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div>
    <li class="catalog__item">
      <router-link :to="{ name: 'product', params: { id: product.id } }" class="catalog__pic">
        <img :src="product.preview.file.url" :alt="product.title">
      </router-link>

      <h3 class="catalog__title">
        <a @click.prevent="openQuickView(product.id)" @keypress="openQuickView">
          {{ product.title }}
        </a>
      </h3>

      <span class="catalog__price">
        {{ product.price | numberFormat }} ₽
      </span>

      <ul class="colors colors--black">
        <li class="colors__item" v-for="color in product.colors" :key="color.color.id">
          <label class="colors__label">
            <input class="colors__radio sr-only" type="radio" name="color-1">
            <span class="colors__value" :style="{ backgroundColor: color.color.code }">
            </span>
          </label>
        </li>
      </ul>
    </li>
    <BaseModal :open="isQuickViewOpen" @close="closeQuickView">
      <ProductQuickView :product-id="currentProductId"></ProductQuickView>
    </BaseModal>
  </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import BaseModal from '@/components/BaseModal.vue';
import ProductQuickView from './ProductQuickView.vue';

export default {
  data() {
    return {
      currentProductId: null,
    };
  },
  props: ['product'],
  filters: { numberFormat },
  components: { BaseModal, ProductQuickView },
  methods: {
    openQuickView(productId) {
      this.currentProductId = productId;
    },
    closeQuickView(value) {
      this.isQuickViewOpen = value;
    },
  },
  computed: {
    isQuickViewOpen: {
      get() {
        return !!this.currentProductId;
      },
      set(isOpen) {
        if (!isOpen) {
          this.currentProductId = 0;
        }
      },
    },
  },
};
</script>
