<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <li class="options__item">
    <label class="options__label">
      <input class="options__radio sr-only" v-model="dataValue" type="radio" :value="variant.id" name="payment" checked="">
      <span class="options__value">
        {{ variant.title }}
      </span>
    </label>
  </li>
</template>

<script>
export default {
  props: ['variant'],

  computed: {
    dataValue: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value);
      },
    },
  },
};
</script>
