<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable max-len -->
<template>
  <div v-if="cartLoading" class="loading">
    <img class="cart-gif" src="@/assets/gif/blocks.gif" alt="Двигающиеся блоки"></div>
  <div v-else-if="cartLoadingFailed" class="not-found-container">
    <span>Ошибка :( Попробуйте </span>
    <router-link :to="{ name: 'cart' }" custom v-slot="{ navigate }">
      <button @click="navigate" @keypress.enter="navigate" role="link">
        еще раз
      </button>
    </router-link>
  </div>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'main' }" class="breadcrumbs__link">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ products.length | pluralize }}
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">

            <CartItem v-for="item in products" :item="item" :key="item.id" />

          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы посчитаем стоимость доставки на следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link :to="{ name: 'order' }" custom v-slot="{ navigate }" class="cart__button button button--primery" type="submit">
            <button @click="navigate" @keypress.enter="navigate" role="link">
              Оформить заказ
            </button>
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import pluralize from '@/helpers/pluralize';
import CartItem from '@/components/CartItem.vue';
import { mapGetters, mapActions } from 'vuex';

export default {
  data() {
    return {
      cartLoadingFailed: false,
      cartLoading: false,
    };
  },
  filters: { numberFormat, pluralize },
  components: { CartItem },
  computed: {
    ...mapGetters({ totalPrice: 'cartTotalPrice' }),

    products() {
      return this.$store.state.cartProductsData;
    },
  },
  methods: {
    ...mapActions(['loadCart']),

    loadingCart() {
      this.cartLoadingFailed = false;
      this.cartLoading = true;
      this.loadCart()
        // eslint-disable-next-line no-return-assign
        .catch(() => this.cartLoadingFailed = true)
        // eslint-disable-next-line no-return-assign
        .then(() => this.cartLoading = false);
    },
  },
  created() {
    this.loadingCart();
  },
};
</script>

<style scoped>
  .loading {
    display: flex;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    justify-content: center;
    align-items: center;
  }
  .cart-gif {
    width: 100px;
    height: 100px;
  }
  .not-found-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
}
</style>
