<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
    <li class="sizes__item" >
          <label class="sizes__label">
            <input class="sizes__radio sr-only" type="radio" name="sizes-item"
            :value="offer.id"
            v-model="dataValue"
            checked>
            <span class="sizes__value" v-for="propValue in offer.propValues" :key="propValue.productProp.id">
              {{ propValue.value }}
            </span>
          </label>
        </li>
</template>

<script>
export default {
  props: ['offer', 'offerId'],
  computed: {
    dataValue: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('update:offerId', value);
      },
    },
  },
};
</script>
