<!-- eslint-disable no-plusplus -->
<!-- eslint-disable max-len -->
<template>
  <ul class="catalog__pagination pagination">
    <li class="pagination__item">
      <a @click.prevent="prevPage(page)" href="#" class="pagination__link pagination__link--arrow" aria-label="Предыдущая страница"
      :class="{'pagination__link--disabled':page === 1 }">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-left"></use>
        </svg>
      </a>
    </li>
    <li class="pagination__item" v-for="pageNumber in pages" :key="pageNumber">
      <a href="#" class="pagination__link" :class="{'pagination__link--current':pageNumber === page}"
      @click.prevent="paginate(pageNumber)">
        {{ pageNumber }}
      </a>
    </li>
    <li class="pagination__item">
      <a @click.prevent="nextPage(page)" class="pagination__link pagination__link--arrow" href="#" aria-label="Следующая страница"
      :class="{'pagination__link--disabled':page === pages }">
        <svg width="8" height="14" fill="currentColor">
          <use xlink:href="#icon-arrow-right"></use>
        </svg>
      </a>
    </li>
  </ul>
</template>

<script>
export default {
  model: {
    prop: 'page',
    event: 'paginate',
  },
  props: ['page', 'count', 'perPage'],
  computed: {
    pages() {
      return Math.ceil(this.count / this.perPage);
    },
  },
  methods: {
    paginate(page) {
      this.$emit('paginate', page);
    },
    nextPage(page) {
      let nextPage = page;
      if (page < this.pages) {
        this.$emit('paginate', nextPage += 1);
      }
    },
    prevPage(page) {
      let prevPage = page;
      if (prevPage > 1) {
        this.$emit('paginate', prevPage -= 1);
      }
    },
  },
};
</script>
