<!-- eslint-disable no-unused-vars -->
<!-- eslint-disable no-restricted-syntax -->
<!-- eslint-disable no-unused-vars -->
<!-- eslint-disable vuejs-accessibility/form-control-has-label -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<!-- eslint-disable max-len -->
<template>
  <div v-if="productLoading" class=" loading">
    <img class="cart-gif" src="@/assets/gif/blocks.gif" alt="Двигающиеся блоки">
  </div>
  <div v-else-if="!productData">
    <h2>Ошибка :( </h2>
    <router-link :to="{ name: 'main' }" custom v-slot="{ navigate }">
      <button @click="navigate" @keypress.enter="navigate" role="link" class="error-btn">
        в каталог
      </button>
    </router-link>
  </div>
  <div v-else >
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'main' }" class="breadcrumbs__link">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'main', params: { id: category.id } }" class="breadcrumbs__link">
            {{ category.title }}
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            {{ product.title }}
          </a>
        </li>
      </ul>
    </div>

    <section class="item">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img width="570" height="570" :src="product.preview.file.url" :alt="product.title">
        </div>
        <ul class="pics__list">
          <li class="pics__item">
            <a href="" class="pics__link pics__link--current">
              <img width="98" height="98" :src="product.preview.file.url" :alt="product.title">
            </a>
          </li>
          <li class="pics__item">
            <a href="" class="pics__link">
              <img width="98" height="98" :src="product.preview.file.url" :alt="product.title">
            </a>
          </li>
          <li class="pics__item">
            <a href="" class="pics__link">
              <img width="98" height="98" :src="product.preview.file.url" :alt="product.title">
            </a>
          </li>
          <li class="pics__item">
            <a class="pics__link" href="#">
              <img width="98" height="98" :src="product.preview.file.url" :alt="product.title">
            </a>
          </li>
        </ul>
      </div>

      <div class="item__info">
        <span class="item__code">
          Артикул: {{ ProductArticle(productOfferId) }}
        </span>
        <h2 class="item__title">
          {{ ProductTitle(productOfferId) }}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="addToCart">
            <b class="item__price">
              {{ ProductPrice(productOfferId) | numberFormat }} ₽
            </b>

            <ProductColor v-model="colorId" :product="product" />

            <fieldset class="form__block">
              <legend class="form__legend">{{ product.offers[0].propValues[0].productProp.title }}:</legend>
              <ul class="sizes sizes--primery">
                <ProductProp v-for="offer in product.offers" :key="offer.id" :offer="offer" :offer-id.sync="productOfferId" />
              </ul>
            </fieldset>

            <div class="item__row">
              <div class="form__counter">

                <ReduceButton @click="reduceAmount" />

                <input type="text" v-model.number="productAmount">

                <IncreaseButton @click="increaseAmount" />
              </div>

              <button class="button button--primery" type="submit" :disabled="productAddSending">
                В корзину
              </button>
              <BaseModal :open="isShowAddedMsg" @close="closeModal">Товар добавлен в корзину</BaseModal>
            </div>
            <div v-show="productAddSending">Добавляем товар в корзину</div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import BaseModal from '@/components/BaseModal.vue';
import numberFormat from '@/helpers/numberFormat';
import IncreaseButton from '@/components/IncreaseButton.vue';
import ReduceButton from '@/components/ReduceButton.vue';
import ProductColor from '@/components/ProductColor.vue';
import ProductProp from '@/components/ProductProp.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      productOfferId: 0,
      colorId: 0,
      currentPrice: 0,
      productAmount: 1,

      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productAddSending: false,
      isShowAddedMsg: false,

      tabName: 'Descr',
    };
  },
  props: {
    productId: {
      type: [Number, String],
      required: true,
    },
  },
  filters: { numberFormat },
  components: {
    ProductColor,
    ProductProp,
    IncreaseButton,
    ReduceButton,
    BaseModal,
  },
  computed: {
    product() {
      return this.productData;
    },
    category() {
      return this.productData.category;
    },
  },
  methods: {
    closeModal(value) {
      this.isShowAddedMsg = value;
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      axios
        .get(`${API_BASE_URL}/api/products/${this.productId}`)
        // eslint-disable-next-line no-return-assign
        .then((response) => this.productData = response.data)
        // eslint-disable-next-line no-return-assign
        .catch(() => this.productLoadingFailed = true)
        // eslint-disable-next-line no-return-assign
        .then(() => this.productLoading = false);
    },
    firstColor() {
      this.colorId = this.product.colors[0].color.id;
    },
    firstProp() {
      this.productOfferId = this.product.offers[0].id;
    },
    ProductPrice(productOfferId) {
      let price = 0;
      // eslint-disable-next-line no-restricted-syntax
      for (const offer of this.product.offers) {
        if (offer.id === productOfferId) {
          price = offer.price;
        }
      }
      return price;
    },
    ProductTitle(productOfferId) {
      let title = '';
      // eslint-disable-next-line no-restricted-syntax
      for (const offer of this.product.offers) {
        if (offer.id === productOfferId) {
          title = offer.title;
        }
      }
      return title;
    },
    ProductArticle(productOfferId) {
      let article = '';
      // eslint-disable-next-line no-restricted-syntax
      for (const offer of this.product.offers) {
        if (offer.id === productOfferId) {
          article = offer.id;
        }
      }
      return article;
    },
    increaseAmount() {
      this.productAmount += 1;
    },
    reduceAmount() {
      if (this.productAmount >= 2) {
        this.productAmount -= 1;
      }
    },
    addToCart() {
      this.productAddSending = true;
      this.isShowAddedMsg = false;

      this.$store.dispatch('addProductToCart', {
        productOfferId: this.productOfferId,
        colorId: this.colorId,
        quantity: this.productAmount,
      })
        .then(() => {
          this.productAddSending = false;
          this.isShowAddedMsg = true;
        });
    },
  },
  watch: {
    product() {
      this.firstColor();
      this.firstProp();
    },
    productOfferId() {
      this.ProductPrice();
      this.ProductTitle();
      this.ProductArticle();
    },
  },
  created() {
    this.loadProduct();
  },
};
</script>

<style scoped>
.item {
  grid-template-columns: 1fr;
}
.loading {
  display: flex;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  justify-content: center;
  align-items: center;
}

.cart-gif {
  width: 100px;
  height: 100px;
}

.keep {
  margin-top: 30px;
}
</style>
