<!-- eslint-disable max-len -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div v-if="totalAmount === 0" class="loading">
    <img class="cart-gif" src="@/assets/gif/blocks.gif" alt="Двигающиеся блоки">
  </div>
  <main v-else-if="formLoading">
    <div style="display: flex; flex-direction: column; align-items: center; justify-content: center; width: 100vw; height: 100vh; background-color: #9eff00">
      <h2 style="color:beige">Отправляю заказ</h2>
      <img class="cart-gif" src="@/assets/gif/cart.gif" alt="Корзинка мчиться">
    </div>
  </main>
  <main v-else class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'main' }" class="breadcrumbs__link">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'cart' }" class="breadcrumbs__link">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{ totalAmount | pluralize }}
      </span>
    </div>

    <section class="cart">
          <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
            <div class="cart__field">
              <div class="cart__data">

                <BaseFormText v-model="formData.name" title="ФИО" placeholder="Введите ваше полное имя" :error="formError.name" />
                <BaseFormText v-model="formData.address" title="Адрес доставк" placeholder="Введите ваш адрес" :error="formError.address" />
                <BaseFormText v-model="formData.phone" title="Телефон" placeholder="Введите ваш телефон" :error="formError.phone" type="numder" />
                <BaseFormText v-model="formData.email" title="Email" placeholder="Введите ваш Email" :error="formError.email" type="email" />
                <BaseFormTextarea title="Комментарий к заказу" v-model="formData.comment" :error="formError.comment" />
              </div>

              <div class="cart__options">
                <h3 class="cart__title">Доставка</h3>
                <ul class="cart__options options">
                  <BaseRadioDelivery v-model.number="deliveryTypeId" v-for="variant in delivery" :key="variant.id" :variant="variant" />
                </ul>

                <h3 class="cart__title">Оплата</h3>
                <ul class="cart__options options">
                  <BaseRadioPayment v-model.number="paymentTypeId" v-for="variant in payment" :key="variant.id" :variant="variant" />
                </ul>
              </div>
            </div>

            <div class="cart__block">
              <div class="cart__orders">

                <OrderCartItem v-for="item in products" :key="item.id" :item="item"></OrderCartItem>

              </div>

              <div class="cart__total" v-if="chousenDelivery">
                <p>  {{ chousenDelivery.title }}: <b>{{ chousenDelivery.price }} ₽</b></p>
                <p>Итого: <b>{{ totalAmount | pluralize }}</b> на сумму <b>{{ totalPrice | numberFormat }} ₽</b></p>
              </div>

              <button class="cart__button button button--primery" type="submit">
                Оформить заказ
              </button>
            </div>
            <div class="cart__error form__error-block" v-if="formErrorMessage">
              <h4>Заявка не отправлена!</h4>
              <p>
                {{ formErrorMessage }}
              </p>
            </div>
          </form>
    </section>
  </main>
</template>

<script>
import BaseRadioDelivery from '@/components/BaseRadioDelivery.vue';
import BaseRadioPayment from '@/components/BaseRadioPayment.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import OrderCartItem from '@/components/OrderCartItem.vue';
import numberFormat from '@/helpers/numberFormat';
import pluralize from '@/helpers/pluralize';
import { mapGetters, mapActions } from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  name: 'OrderPage',
  components: {
    BaseRadioDelivery, BaseRadioPayment, BaseFormText, BaseFormTextarea, OrderCartItem,
  },
  filters: {
    numberFormat,
    pluralize,
  },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      formLoading: false,

      deliveryTypeId: 1,
      paymentTypeId: 2,
    };
  },
  computed: {
    ...mapGetters({ totalPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount' }),

    products() {
      return this.$store.state.cartProductsData;
    },
    delivery() {
      return this.$store.state.deliveryData;
    },
    payment() {
      return this.$store.state.paymentData;
    },
    chousenDelivery() {
      return this.delivery[this.deliveryTypeId - 1];
    },
  },
  methods: {
    ...mapActions(['deliveriesVar', 'paymentVar']),

    updatePaymentVar() {
      this.paymentVar(this.deliveryTypeId);
    },

    order() {
      this.formError = {};
      this.formErrorMessage = '';
      this.formLoading = true;

      axios
        .post(`${API_BASE_URL}/api/orders`, {
          ...this.formData,
          deliveryTypeId: this.deliveryTypeId,
          paymentTypeId: this.paymentTypeId,
        }, {
          params: {
            userAccessKey: this.$store.state.userAccessKey,
          },
        })
        .then((response) => {
          this.$store.commit('resetCart');
          this.$store.commit('updateOrderInfo', response.data);
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } });
        })
        .catch((error) => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
        })
        // eslint-disable-next-line no-return-assign
        .then(() => this.formLoading = false);
    },
  },
  watch: {
    deliveryTypeId() {
      this.updatePaymentVar();
    },
  },
  created() {
    this.deliveriesVar();
    this.paymentVar(this.deliveryTypeId);
  },
};
</script>

<style scoped>
  .loading {
    display: flex;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    justify-content: center;
    align-items: center;
  }
  .cart-gif {
    width: 100px;
    height: 100px;
  }
  .error-btn {
    background-color: #fff;
  }
</style>
