import Vue from 'vue';
// eslint-disable-next-line import/no-extraneous-dependencies
import Portal from 'vue2-portal';
import App from './App.vue';
import router from './router';
import store from './store';

Vue.config.productionTip = false;
Vue.use(Portal);
new Vue({
  router,
  store,
  render: (h) => h(App),
}).$mount('#app');
