<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <div class="cart__options">
    <h3 class="cart__title">Доставка</h3>
    <ul class="cart__options options">
      <BaseRadioDeliveryVue v-for="variant in delivery" :key="variant.id" :variant="variant"/>
    </ul>

    <h3 class="cart__title">Оплата</h3>
    <ul class="cart__options options">
      <BaseRadioPayment v-for="variant in payment" :key="variant.id" :variant="variant"/>
    </ul>
  </div>
</template>

<script>
import { mapActions } from 'vuex';
import BaseRadioPayment from './BaseRadioPayment.vue';
import BaseRadioDeliveryVue from './BaseRadioDelivery.vue';

export default {
  data() {
    return {
      deliveryTypeId: 1,
      paymentTypeId: 2,
    };
  },
  components: { BaseRadioPayment, BaseRadioDeliveryVue },
  computed: {
    delivery() {
      return this.$store.state.deliveryData;
    },
    payment() {
      return this.$store.state.paymentData;
    },
  },
  methods: {
    ...mapActions(['deliveriesVar', 'paymentVar']),
  },
  created() {
    this.deliveriesVar();
    this.paymentVar(this.deliveryTypeId);
  },
};
</script>
