<!-- eslint-disable max-len -->
<!-- eslint-disable vue/no-mutating-props -->
<!-- eslint-disable vuejs-accessibility/label-has-for -->
<template>
  <aside class="filter">
    <h2 class="filter__title">Фильтры</h2>

    <form class="filter__form form" action="#" method="get">
      <fieldset class="form__block">
        <legend class="form__legend">Цена</legend>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="min-price" v-model.number="currentPriceFrom">
          <span class="form__value">От</span>
        </label>
        <label class="form__label form__label--price">
          <input class="form__input" type="text" name="max-price" v-model.number="currentPriceTo">
          <span class="form__value">До</span>
        </label>
      </fieldset>

      <fieldset class="form__block">
        <legend class="form__legend">Категория</legend>
        <label class="form__label form__label--select">
          <select class="form__select" type="text" name="category" v-model="currentCategory">
            <option value="0">Все категории</option>
            <option :value="category.id" v-for="category in categories" :key="category.id">{{ category.title }}</option>
          </select>
        </label>
      </fieldset>

      <!-- <fieldset class="form__block">
        <legend class="form__legend">Цвет</legend>
        <ul class="colors">
          <li class="colors__item" v-for="color in colors" :key="color.id">
            <label class="colors__label">
              <input class="colors__radio sr-only" type="checkbox" checked v-model="currentColor" :value="color.id">
              <span class="colors__value" :style="{ backgroundColor: color.code }">
              </span>
            </label>
          </li>
        </ul>
      </fieldset> -->

      <div v-show="currentCategory > 0">
        <div class="props-loading" v-if="propsLoading">
          <img class="props-cart-gif" src="@/assets/gif/blocks2.gif" alt="Двигающиеся блоки">
        </div>
        <div v-else-if="propsLoadingFailed">
          <span>Ошибка :( Попробуйте перезагрузить страницу</span>
          <router-link :to="{ name: 'main' }" custom v-slot="{ navigate }">
            <button @click="navigate" @keypress.enter="navigate" role="link" class="error-btn">
              Перезагрузить
            </button>
          </router-link>
        </div>
        <fieldset v-else class="form__block">
          <legend v-if="propsCategories" class="form__legend">{{ propsCategories.title }}</legend>
          <ul class="check-list">
            <li class="check-list__item" v-for="prop in propsCategories.availableValues" :key="prop.index">
              <label class="check-list__label">
                <input class="check-list__check sr-only" type="checkbox" v-model="currentProp" :value="prop.value">
                <span class="check-list__desc">
                  {{prop.value}}
                  <span>({{ prop.productsCount }})</span>
                </span>
              </label>
            </li>
          </ul>
        </fieldset>
      </div>

      <button class="filter__submit button button--primery" type="submit" @click.prevent="submit">
        Применить
      </button>

      <button v-if="currentPriceFrom || currentPriceTo || currentCategory" class="filter__reset button button--second" type="button" @click.prevent="reset">
        Сбросить
      </button>
    </form>
  </aside>
</template>

<script>
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  data() {
    return {
      currentPriceFrom: 0,
      currentPriceTo: 0,
      currentCategory: 0,
      // currentColor: [],
      currentProp: [],

      categoriesData: null,
      propsCategoriesData: null,
      // colorsData: null,

      propsLoading: false,
      propsLoadingFailed: false,
    };
  },
  props: ['priceFrom', 'priceTo', 'categoryId', 'code', 'prop'],
  computed: {
    categories() {
      return this.categoriesData ? this.categoriesData.items : [];
    },
    propsCategories() {
      return this.propsCategoriesData ? this.propsCategoriesData.productProps[0] : [];
    },
    codeProp() {
      return this.propsCategoriesData ? this.propsCategories.code : null;
    },
    // colors() {
    //   return this.colorsData ? this.colorsData.items : [];
    // },
  },

  methods: {
    loadCategories() {
      axios
        .get(`${API_BASE_URL}/api/productCategories`)
        // eslint-disable-next-line no-return-assign
        .then((response) => this.categoriesData = response.data);
    },
    loadPropsOfCategories() {
      this.propsLoading = true;
      this.propsLoadingFailed = false;
      if (this.currentCategory > 0) {
        axios
          .get(`${API_BASE_URL}/api/productCategories/${this.currentCategory}`)
          // eslint-disable-next-line no-return-assign
          .then((response) => this.propsCategoriesData = response.data)
          // eslint-disable-next-line no-return-assign
          .catch(() => this.propsLoadingFailed = true)
          // eslint-disable-next-line no-return-assign
          .then(() => this.propsLoading = false);
      }
    },
    // productColors() {
    //   this.currentColor = [];
    //   axios.get(`${API_BASE_URL}/api/colors`)
    //     // eslint-disable-next-line no-return-assign
    //     .then((response) => this.colorsData = response.data);
    // },
    submit() {
      this.$emit('update:priceFrom', this.currentPriceFrom);
      this.$emit('update:priceTo', this.currentPriceTo);
      this.$emit('update:categoryId', this.currentCategory);
      // this.$emit('update:color', this.currentColor);
      this.$emit('update:code', this.codeProp);
      this.$emit('update:prop', this.currentProp);
    },
    reset() {
      this.$emit('update:priceFrom', 0);
      this.$emit('update:priceTo', 0);
      this.$emit('update:categoryId', 0);
      // this.$emit('update:color', []);
      this.$emit('update:code', null);
      this.$emit('update:prop', []);

      this.currentPriceFrom = 0;
      this.currentPriceTo = 0;
      this.currentCategory = 0;
      // this.currentColor = [];
      this.currentProp = [];
    },
  },

  watch: {
    priceFrom(value) {
      this.currentPriceFrom = value;
    },
    priceTo(value) {
      this.currentPriceTo = value;
    },
    categoryId(value) {
      this.currentCategory = value;
    },
    // color(value) {
    //   this.currentColor = value;
    // },
    currentCategory() {
      this.loadPropsOfCategories();
    },
  },
  created() {
    this.loadCategories();
    // this.productColors();
    this.loadPropsOfCategories();
  },
};
</script>

<style scoped>
.props-loading {
  position: absolute;
  top: -5px;
  right: -3px;
  padding: 3px;
}

.props-cart-gif {
  width: 15px;
  height: 15px;
}
</style>
