/* eslint-disable max-len */
import Vue from 'vue';
import Vuex from 'vuex';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    userAccessKey: null,
    cartProductsData: [],
    indicatorLoadin: false,

    deliveryData: [],
    deliveryTypeId: 1,
    paymentData: [],
    paymentTypeId: 2,

    orderInfo: null,
    orderInfoLoadingFailed: false,
  },
  mutations: {
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    deleteCartProduct(state, basketItemId) {
      state.cartProductsData = state.cartProductsData.filter((item) => item.id !== basketItemId);
    },
    updateCartProductAmount(state, { productOfferId, quantity }) {
      const item = state.cartProductsData.find((i) => i.productOffer.id === productOfferId);

      if (item) {
        item.quantity = quantity;
      }
    },
    updateDeliveryData(state, deliveryData) {
      state.deliveryData = deliveryData;
    },
    updatePaymentData(state, paymentData) {
      state.paymentData = paymentData;
    },
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProductsData = [];
    },
  },
  getters: {
    cartTotalPrice(state) {
      return state.cartProductsData.reduce(
        (acc, item) => item.price * item.quantity + acc,
        0,
      );
    },
    cartTotalAmount(state) {
      return state.cartProductsData.length;
    },
  },
  actions: {
    loadCart(context) {
      return axios
        .get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        // eslint-disable-next-line no-return-assign
        .then((response) => {
          // если глобальное хранилище пустое, тогда записываем туда ключ пользователя
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey);
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }
          context.commit('updateCartProductsData', response.data.items);
        });
    },
    addProductToCart(context, { productOfferId, colorId, quantity }) {
      this.state.indicatorLoadin = true;
      return axios
        .post(`${API_BASE_URL}/api/baskets/products`, {
          productOfferId,
          colorId,
          quantity,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .then(() => { this.state.indicatorLoadin = false; });
    },
    updateCartProductAmount(context, { basketItemId, quantity }) {
      context.commit('updateCartProductAmount', { basketItemId, quantity });

      return axios
        .put(`${API_BASE_URL}/api/baskets/products`, {
          basketItemId,
          quantity,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('updateCartProductsData');
        });
    },
    deleteProductInCart(context, basketItemId) {
      this.state.indicatorLoadin = true;
      return axios
        .delete(`${API_BASE_URL}/api/baskets/products`, {
          data: { basketItemId },
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .then(() => { this.state.indicatorLoadin = false; });
    },
    loadOrderInfo(context, orderId) {
      this.state.orderInfoLoadingFailed = false;
      return axios
        .get(`${API_BASE_URL}/api/orders/${orderId}`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        })
        // eslint-disable-next-line no-return-assign
        .catch(() => this.state.orderInfoLoadingFailed = true);
    },
    deliveriesVar(context) {
      return axios
        .get(`${API_BASE_URL}/api/deliveries`)
        // eslint-disable-next-line no-return-assign
        .then((response) => {
          context.commit('updateDeliveryData', response.data);
        });
    },
    paymentVar(context, deliveryTypeId) {
      return axios
        .get(`${API_BASE_URL}/api/payments`, {
          params: {
            deliveryTypeId,
          },
        })
        // eslint-disable-next-line no-return-assign
        .then((response) => {
          context.commit('updatePaymentData', response.data);
        });
    },
  },
});
